import styles from './card_slot.module.scss';
import { For, JSX, ParentProps, children } from "solid-js";

const interactionStyles = Object.freeze({
  none: styles.none,
  dragable: styles.draggable,
  dragging: styles.dragging,
  clickable: styles.clickable,
  immovable: styles.immovable,
  noDrop: styles['no-drop'],
});
export type InteractionStyle = keyof typeof interactionStyles;

export function CardSlotComponent(props: ParentProps<{
  targetCard: JSX.Element,
  targetInteractionStyle: InteractionStyle,
  onDragStart: () => void,
  onDrop: () => void,
}>) {
  return (
    <div class={styles.container}>
      <div
          classList={{ 
            [styles['target-card']]: true,
            [interactionStyles[props.targetInteractionStyle]]: true,
          }}
          onMouseDown={props.onDragStart}
          onMouseUp={props.onDrop}>
        {props.targetCard}
      </div>
      <div class={styles['played-cards']}>
        <For each={children(() => props.children).toArray()}>
          {child => (
            <div class={styles['played-card']}>{child}</div>
          )}
        </For>
      </div>
    </div>
  );
}
