import { Card, CardFaceType, CardSlot } from "model/domain";
import { CardSlotComponent as CardSlotComponentImpl, InteractionStyle } from "./card_slot";
import { CardSlotsComponent } from "./card_slots";
import { Component, For } from "solid-js";
import { ComponentManager } from "components/component_manager";
import { CardSlotController } from "./card_slot_controller";
import { UnreachableError } from "base/unreachable_error";

export function createCardSlots(
  CardSlotComponent: Component<{ model: CardSlot }>,
) {
  const Component = (props : {
    model: readonly CardSlot[],
  }) => {
    return (
      <CardSlotsComponent
        CardSlotComponent={CardSlotComponent}
        cardSlots={props.model}/>
    );
  };
  return {
    Component,
  };
}

export function createCardSlotManager(
    CardComponent: Component<{ model: Card }>,
    onDragStart: (cardSlot: CardSlot) => void,
    onDrop: (cardSlot: CardSlot) => void,
) {
  function createCardSlot(cardSlot: CardSlot) {
    const controller = new CardSlotController();
    const internalOnDragStart = () => onDragStart(cardSlot);
    const internalOnDrop = () => onDrop(cardSlot);
    
    function Component() {
      return (
          <CardSlotComponentImpl
              targetCard={cardSlot.targetCard
                  && <CardComponent model={cardSlot.targetCard}/>
              }
              targetInteractionStyle={computeTargetCardInteractionStyle(cardSlot.targetCard)}
              onDragStart={internalOnDragStart}
              onDrop={internalOnDrop}
          >
            <For each={cardSlot.playedCards}>
              {card => {
                return <CardComponent model={card}/>
              }}
            </For>
          </CardSlotComponentImpl>
      );
    }
    return {
      controller,
      Component,
    };
  };
  return new ComponentManager(createCardSlot);
}

// TODO move out somewhere
function computeTargetCardInteractionStyle(card: Card | undefined): InteractionStyle {
  if (card == null) {
    return 'none';
  }
  const face = card.type.faces[card.visibleFaceIndex];
  switch (face.type) {
    case CardFaceType.Choice:
      return 'clickable';
    case CardFaceType.ChoiceBack:
      return 'clickable';
    case CardFaceType.Resource:
      return 'dragable';
    case CardFaceType.ResourceBack:
      return 'immovable';
    default:
      throw new UnreachableError(face);
  }
}
